* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Dự án Typing React */
.App {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: radial-gradient(#3d3d42, #191a1f);
  font-family: Poppins;
  color: #eee;
}

.home .title {
  font-size: 80px;
  font-weight: 700;
  text-shadow: 0 10px 10px #000;
}

.home .author {
  margin-top: 10px;
  text-align: center;
  font-weight: 600;
}

.btnPlay {
  display: block;
  margin: 20px auto;
  text-align: center;
  background-color: #191a21;
  color: #eee;
  padding: 10px 24px;
  font-size: large;
  letter-spacing: 5px;
  box-shadow: 0 10px 10px #0005;
  cursor: pointer;
  outline: none;
  border: none;
  border-radius: 10px;
}

@keyframes showHome {
  to {
    opacity: 1;
  }
}

.home {
  opacity: 0;
  animation: showHome 2s .3s ease-in-out 1 forwards;
}

.playing {
  display: grid;
  grid-template-rows: 70% 1fr;
  height: 100vh;
  padding: 50px;
  text-align: center;
  gap: 10px;
  opacity: 0;
  animation: showHome 2s .3s ease-in-out 1 forwards;
}

.playing .list {
  list-style: none;
  padding: 50px;
  text-align: left;
  background-image: linear-gradient(45deg, #5556a0, #ab84b4);
  border-radius: 20px;
  box-shadow: 0 2px 0 #fff;
}

.playing .list li {
  display: inline-block;
  padding: 10px;
  fon-size: x-large;
  font-weight: 500;
  text-shadow: 0 2px 2px #0009;
  position: relative;
}

.playing .inputForm input {
  width: 100%;
  padding: 20px 0;
  outline: none;
  border: none;
  text-align: center;
  font-size: x-large;
  background-color: #0005;
  color: #fff;
  border-radius: 20px;
  font-family: Poppins;
}

.playing li.true {
  color: greenyellow;
}

.playing li.false {
  color: red;
}

.playing li.true::after,
.playing li.false::after {
  position: absolute;
  inset: 0 0 0 0;
  content: '+1';
  text-align: center;
  animation: showPoint .5s linear 1 forwards;
}

.playing li.false::after {
  content: '-1';
}

@keyframes showPoint {
  0% { opacity: 0; }
  50% { opacity: 1 }
  100% { opacity: 0; transform: translateY(-50px) }
}

.endGame {
  opacity: 0;
  animation: showHome 2s .3s ease-in-out 1 forwards;
}

.endGame .result {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 50px;
  text-align: center;
  font-size: 20px;
  text-shadow: 0 10px 10px #000;
}

.endGame .title {
  font-size: 60px;
  font-weight: 600;
  text-transform: uppercase;
}

.endGame .number {
  font-size: 50px;
  font-weight: 700;
}

