.card{
    width: 100%;
    max-width: 400px;
    padding: 20px;
    background: white;
    border-radius: 16px;
    box-shadow: 0 1px 3px 0 rgb(0 0 0 / 0.1), 0 1px 2px -1px rgb(0 0 0 / 0.1);
}
.card-image{
    height: 360px;
    margin-bottom: 20px;
}
.card-image img{
    width: 100%;
    display: block;
    object-fit: cover;
}
.card-title{
    font-size: 20px;
    font-weight: 700;
    text-transform: uppercase;
    margin-bottom: 20px;
}
.card-description{
    font-size: 16px;
    font-weight: 500;
    line-height: 1.5;
    letter-spacing: 0.3px;
    margin-bottom: 40px;
}
.card-link{
    font-size: 16px;
    font-weight: 600;
    text-decoration: none;
    outline: none;
    border: none;
    text-align: center;
    padding: 12px 24px;
    cursor: pointer;
    border-radius: 8px;
    display: block;
    background: yellow;
    color: red;
}
